import React from "react";
import "./MovieDetail.css"

    function MovieDetail({ id, year, title, summary, poster, genres }) {
    return <div className="movie_cnt">
        <div className="movie_box">
            <img src={poster} className="poster" alt={title} title={title} />
            <h3 className="title">{title}</h3>
            <div className="genres">
                {genres.map((genre,id) => <p key={id+id} className="genre">{genre}</p>)}
            </div>
            <h5 className="year">{year}</h5>
            <p className="summary">{summary}</p>
            
        </div>
    </div>
}

export default MovieDetail;